.spinner.lds-ring{position:relative;top:50%;left:50%;width:64px;height:64px;display:inline-block;border-radius:5px}.spinner.lds-ring div{box-sizing:border-box;display:block;position:absolute;height:51px;margin:6px;border:6px solid #be901e;border-radius:50%;-webkit-animation:lds-ring 1.4s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.4s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#be901e transparent transparent transparent}.spinner.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.spinner.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.spinner.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes lds-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
