.main-header{position:fixed;right:0;left:0;top:0;background-color:white;z-index:1000;border-bottom:1px solid #e4e4e4}.main-header .wrapper{padding:5px 0}.main-header .right{float:right}.main-header .left{line-height:60px;float:left}.main-header .user-login-holder{float:left}.main-header .user-login-holder span{font-family:'IRANSans', Tahoma;font-weight:500;line-height:1.7}.main-header .user-holder{direction:rtl}.main-header .rest-logo-holder{display:inline-block;direction:rtl;position:relative;cursor:pointer}.main-header .rest-logo-holder figure{border-radius:10px;vertical-align:middle;display:inline-block;overflow:hidden}.main-header .logo-holder{width:60px;height:60px;border-radius:5px}.main-header .logo-holder img{display:block;height:100%}@media only screen and (min-width: 767px){.main-header{z-index:2500}.main-header [class*='fo-'],.main-header [class^='fo-']{font-size:22px;font-size:1.375rem}}
